.wrapper {
  position: relative;
  margin: 0;
  padding: 0;
    top: 0;
    left: 0;
    width: 91vw;
    height: 100vh;
    overflow-y:scroll;
    background-color: #ffffff;
    border: 4px solid #2473D5;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    scroll-behavior: smooth;
}
.confirmation {
  background: #2473D5;
  color: #fff5f5;
  padding: 15px 25px;
  position: fixed;
  bottom: 60px;
  left: 20px;
  font-weight: 700;
  font-size: medium;
  text-align: center;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  overflow: hidden;
  border: 3px solid black;
  transition: 0.4s opacity linear;
  -webkit-transition: 0.4s opacity linear;
  -moz-transition: 0.4s opacity linear;
  -ms-transition: 0.4s opacity linear;
  -o-transition: 0.4s opacity linear;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  z-index: 5;
}