.wrapper {
    position: fixed;
    margin: 0;
    padding: 0;
    top: 0;
    right: 0;
    width: 9vw;
    height: 100vh;
    background-color: #ffffff;
    border: 4px solid #2473D5;
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    background-color: #2473D5;
    color: black;
    
    

    li {
     display: block;
     width: 100%;
     background-color: #fff;
     border: 2px solid black;
     text-align: center;
     /*margin: 3px 0;*/
     transition: background-color 0.15s 0.05s linear,
     color 0.15s 0.05s linear;
     -webkit-transition: background-color 0.15s 0.05s linear,
     color 0.15s 0.05s linear;
     -moz-transition: background-color 0.15s 0.05s linear,
     color 0.15s 0.05s linear;
     -ms-transition: background-color 0.15s 0.05s linear,
     color 0.15s 0.05s linear;
     -o-transition: background-color 0.15s 0.05s linear,
     color 0.15s 0.05s linear;
     border-radius: 4px;
     -webkit-border-radius: 4px;
     -moz-border-radius: 4px;
     -ms-border-radius: 4px;
     -o-border-radius: 4px;
    }
    li:hover{
    background-color: #2473D5;
    color: #ffffff;
}
li a {
    text-decoration: none;
    color: inherit;
    display: block;
}
}