.divNear__img {
  display: flex;
  align-items: center;
  justify-content: center;
}
.divNear__img img {
  height: auto;
  width: 30px;
  padding: 0;
  margin: 0;
  margin-right: 7px;
  
  /*border: 2px solid black;*/
 /* border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;*/
}
.item__name {
  grid-area: item__name;
}

.item__card {
  grid-area: item__card;
}

.item__describe {
  grid-area: item__describe;
}
.item__name {
  text-align: center;
  border-bottom: 3px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: large;
  z-index: 2;
  
}
.item__card  {
  border-right: 1.5px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 2;
  transition: background-color 0.15s 0.05s linear,color 0.15s 0.05s linear;
  -webkit-transition: background-color 0.15s 0.05s linear,color 0.15s 0.05s linear;
  -moz-transition: background-color 0.15s 0.05s linear,color 0.15s 0.05s linear;
  -ms-transition: background-color 0.15s 0.05s linear,color 0.15s 0.05s linear;
  -o-transition: background-color 0.15s 0.05s linear,color 0.15s 0.05s linear;
}
.item__describe {
  border-left: 1.45px solid black;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 2;
  transition: background-color 0.15s 0.05s linear,color 0.15s 0.05s linear;
  -webkit-transition: background-color 0.15s 0.05s linear,color 0.15s 0.05s linear;
  -moz-transition: background-color 0.15s 0.05s linear,color 0.15s 0.05s linear;
  -ms-transition: background-color 0.15s 0.05s linear,color 0.15s 0.05s linear;
  -o-transition: background-color 0.15s 0.05s linear,color 0.15s 0.05s linear;
}
.item__name, .item__card, .item__describe {
  background-color: rgba(255, 255, 255, 0.637);
}

.item__card:hover,
.item__describe:hover {
  background-color: #2473D5;
  color: #ffffff;
  
}




.image {
    flex-shrink: 0;
    margin-right: 30px;
    /*width: 70px;
    height: 70px;*/
    width: auto;
    height: 100px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: -1;


    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    -moz-transform: translate(-50%,-50%);
    -ms-transform: translate(-50%,-50%);
    -o-transform: translate(-50%,-50%);
}
.imageNone {
  @extend .image;

  background: url('https://cdn.arstechnica.net/wp-content/uploads/2016/02/5718897981_10faa45ac3_b-640x624.jpg');
  background-position: center;
  background-size: cover;

}


  
.copy_cardModal,.copy_describeModal {
  display: none !important;
  position: fixed;
  top: 50%;
  left: 50%;
  height: 50vh;
  width: 70vw;
  background-color: rgba(36, 116, 213, 0.884);
  transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  -moz-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  -o-transform: translate(-50%,-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: text;
  border-radius: 6px ;
  -webkit-border-radius: 6px ;
  -moz-border-radius: 6px ;
  -ms-border-radius: 6px ;
  -o-border-radius: 6px ;
}
.copy_cardModal:hover,.copy_describeModal:hover {
  color: black;
} 
.copy_cardModal span,.copy_describeModal span {
  position: absolute;
  top: 10px;
  right: 15px;
  cursor: pointer;
  background-color: black;
  padding: 7px;
  border-radius: 1px;
  -webkit-border-radius: 1px;
  -moz-border-radius: 1px;
  -ms-border-radius: 1px;
  -o-border-radius: 1px;
  color: #ffffff;

  transition:  background-color 0.3s ease-in;
  -webkit-transition:  background-color 0.3s ease-in;
  -moz-transition:  background-color 0.3s ease-in;
  -ms-transition:  background-color 0.3s ease-in;
  -o-transition:  background-color 0.3s ease-in;
}

.copy_cardModal span:hover,.copy_describeModal span:hover {
  background-color: red;
}